import {useState, useMemo, useCallback, useEffect} from "react";
import { useSelector } from 'react-redux';
import { calcPositionDetails, openedPositionDetails, calcMarketDetails } from '../../../store/slices/position-slice';
import { IReduxState } from "../../../store/slices/state.interface";
import { useDispatch } from "react-redux";
import { warning, success, info, error } from "../../../store/slices/messages-slice";
import { messages } from "../../../constants/messages";
import { sleep } from "../../../helpers/sleep";
import { PerpetualProtocol, PositionDraft, SupportedChainIds } from "@perp/sdk-curie";
import {AppDispatch} from "../../../store/store";

import {	
	Box,		 
	Button,
	TextField,
	Typography,
	InputAdornment	
} from "@mui/material";

import { useWeb3Context } from "../../../hooks/web3Context";
import {Trade} from '../../../container/trade';
import {AMM} from "../../../container/amm";
import { PositionSide } from "@perp/sdk-curie";
import { metamaskErrorWrap } from "../../../helpers/metamask-error-wrap";

function SendTransaction() {	
	const dispatch = useDispatch<AppDispatch>();
	const { address, provider, chainID, connected } = useWeb3Context();
	const {marketticker} = AMM.useContainer();
	const { collateral } = Trade.useContainer();
	const { _setCollateral} = Trade.useContainer();
	const {side} = Trade.useContainer();
	const {slippage} = Trade.useContainer();
	const {perpetualObject} = Trade.useContainer();
	const outputSymbol = marketticker.substring(0, marketticker.length - 3);	
	const _perpetualObject: PerpetualProtocol = new PerpetualProtocol({
		chainId: SupportedChainIds.OPTIMISM,   		                
		providerConfigs: [
			{				
				rpcUrl: "https://optimism-mainnet.infura.io/v3/dd583584ac144f7984ac22cb0595646e",                							
			},
		],
	});

	
	const isCalculated = useSelector<IReduxState, boolean>((state) => state.positions.isCalculated);
	const newPositionDraft = useSelector<IReduxState, PositionDraft<string> | undefined>(state => {
		return state.positions.newPositionDraft;
	});
	const oppositeAmount = useSelector<IReduxState, string>(state => {
		return state.positions.oppositeAmount;
	});
	const entryPrice = useSelector<IReduxState, string>(state => {
		return state.positions.entryPrice;
	});
	const tradingFee = useSelector<IReduxState, string>(state => {
		return state.positions.tradingFee;
	});
	const priceImpact = useSelector<IReduxState, string>(state => {
		return state.positions.priceImpact;
	});
	const buyingPower = useSelector<IReduxState, string>(state => {
		return state.positions.buyingPower;
	});

	const isDisabled = !isCalculated || collateral === null || collateral.eq(0);

	const handleOpen = useCallback(async () => {
		const hasconnected = perpetualObject.hasConnected();  
		if(hasconnected === false) {
			await perpetualObject.init();
			await perpetualObject.connect({ signer: provider.getSigner() });	
		}		
		let openTx;
		try {
			openTx = await perpetualObject?.clearingHouse?.openPosition(newPositionDraft!, slippage);
			console.log("Open Transaction", openTx);
			await openTx!.transaction.wait();
			dispatch(success({ text: messages.tx_successfully_send }));
			await sleep(0.1);
			dispatch(info({ text: messages.your_position_update_soon }));					
			await dispatch(openedPositionDetails({ address: address, networkID: chainID, provider: provider, perpetualObject: _perpetualObject,
				tickerSymbol: marketticker}));
			dispatch(info({ text: messages.your_position_updated }));
			_setCollateral('');			
		} catch (err: any) {
			metamaskErrorWrap(err, dispatch);
		} finally {			
			
		}	
		
  }, [newPositionDraft, slippage]);

	const CalcPosition = useCallback(
		(loadProvider) => {
			console.log("Calling calc Position");
			dispatch(calcPositionDetails({ connected: connected, networkID: chainID, provider: loadProvider, perpetualObject: perpetualObject,
			side: side, slippage: slippage, tickerSymbol: marketticker, amountInput: collateral}));
		},
		[collateral, slippage, side, marketticker],
	);

	const CalcOpenedPosition = useCallback(
		loadProvider => {
			console.log("Calling calc openedPosition");
			dispatch(openedPositionDetails({ address: address, networkID: chainID, provider: loadProvider, perpetualObject: perpetualObject,
			tickerSymbol: marketticker}));
		},
		[connected, marketticker],
	);

	const CalcMarketDetails = function() {
		dispatch(calcMarketDetails({ perpetualObject: perpetualObject, tickerSymbol: marketticker }));
	}
	

  useEffect(() => {	
		CalcPosition(provider);		
	}, [collateral, slippage, side, marketticker]);

	useEffect(() => {
		CalcOpenedPosition(provider);
	}, [connected, marketticker]);

	useEffect(() => {
		CalcMarketDetails();
		const timer = setInterval(() => CalcMarketDetails(), 300000);	
		return () => clearInterval(timer);	
	}, [marketticker]);

	return (
		<Box sx={{mt: 2}}>
			<Box sx={{mb: 2}}>
				<TextField
					id="outlined-name"
					label="Output"
					type="number"
					disabled
					value={oppositeAmount}
					placeholder="0"
					InputLabelProps={{
						shrink: true,
					}}				
					InputProps={{						
						endAdornment: <InputAdornment position="end">{outputSymbol}</InputAdornment>,
					}}
					sx={{width: '100%',
						'& .MuiFormLabel-root.Mui-disabled, & .MuiFormLabel-root.Mui-focused, & .MuiTypography-root': {color: '#a3a8b7'},
						'& > div': {color: 'white'},
						'& .MuiInputBase-root.MuiOutlinedInput-root.Mui-disabled': {color: '#a3a8b7'},						
						'& .MuiInputBase-root > input': {'-webkit-text-fill-color': '#a3a8b7'},
						'& .MuiInputBase-root.MuiOutlinedInput-root.Mui-disabled .MuiOutlinedInput-notchedOutline': {borderColor: 'rgba(255, 255, 255, 0.16)'}						
					}}					
				/>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Buying Power</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${buyingPower}</Typography>
			</Box>
			<Typography sx={{mx: 'auto', py: 1, textAlign: 'left', color: 'white', fontFamily: 'Montserrat Bold'}}>
				Summary
			</Typography>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Entry Price</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${entryPrice}</Typography>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Trading Fee</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${tradingFee}</Typography>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Price Impact</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>{priceImpact}%</Typography>
			</Box>
			<Button variant="contained" 
				disabled={isDisabled}
				sx={{width: '100%', paddingY: '12px', 
					backgroundColor: side === PositionSide.LONG? 'success.light': '#ef5350', 
					display: 'block',										
					mx: 'auto',
					'&: hover': {backgroundColor: side === PositionSide.LONG? 'success.main': '#f44336'},
					'&: disabled': {backgroundColor: side === PositionSide.LONG? 'success.light': '#ef5350', color: 'black', opacity: 0.8} 
				}}
				onClick={handleOpen}			
			>
				Confirm {side}
			</Button>			
		</Box>
	)
}

export default SendTransaction;