import {useState, useCallback, useEffect} from "react";
import { useSelector } from 'react-redux';
import { IReduxState } from "../../../store/slices/state.interface";

import {	
	Box,	    
	Typography,
} from "@mui/material";

import MarketSelector from "./Marketselector";

function PriceHeader() {
	const isMarketInfoCalculated = useSelector<IReduxState, boolean>(state => {
		return state.positions.isMarketInfoCalculated;
	});	

	const isMarketInfoInitialized = useSelector<IReduxState, boolean>(state => {
		return state.positions.isMarketInfoInitialized;
	});

	// console.log("Market initialized", isMarketInfoInitialized);

	const marketIndexPrice = useSelector<IReduxState, string>(state => {
		return state.positions.marketIndexPrice;
	});
	return (
		<Box sx={{display: 'flex', justifyContent: 'flex-start', alignItems: 'center', 
			backgroundColor: 'rgba(29, 29, 29, 1)',
			flexDirection: {xs: 'column', sm: 'row'},
			borderRadius: '10px',
			py: '4px',
			mb: 1			
		}}>
			<MarketSelector />
			<Box sx={{display: 'flex', justifyContent: 'flex-start',
				borderLeft: {sm: '1px solid rgba(145, 153, 167, 0.8)'},
				pl: {sm: 2, md: 3, xl: 4},
				py: 1
			}}>
				<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
					<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)'}}>Index Price</Typography>
					{isMarketInfoCalculated? <Typography sx={{color: 'rgb(255, 255, 255)'}}>{marketIndexPrice}</Typography> :
						[isMarketInfoInitialized? <Typography sx={{color: 'rgb(255, 255, 255)'}}>{marketIndexPrice}</Typography> : 
						<Typography sx={{color: 'rgb(255, 255, 255)'}}>0.0</Typography>]
					}					
				</Box>
				<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
					<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)'}}>Volume (24h)</Typography>
					<Typography sx={{color: 'rgb(255, 255, 255)'}}>5,798,410.3</Typography>
				</Box>
				<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
					<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)'}}>Funding Rate (8h)</Typography>
					<Typography sx={{color: 'rgb(255, 255, 255)'}}>0.0071%</Typography>
				</Box>
			</Box>
		</Box>
	)
}

export default PriceHeader;