import * as React from 'react';
import { useSelector } from 'react-redux';
import { IReduxState } from "../../../store/slices/state.interface";
import { useDispatch } from "react-redux";
import { warning, success, info, error } from "../../../store/slices/messages-slice";
import { close } from "../../../store/slices/position-slice";
import { messages } from "../../../constants/messages";
import { sleep } from "../../../helpers/sleep";
import { PositionSide } from "@perp/sdk-curie";

import {	
	Box,	
	Button,	   
	Typography  
} from "@mui/material";

import { useWeb3Context } from "../../../hooks/web3Context";
import {Trade} from '../../../container/trade';
import {AMM} from "../../../container/amm";
import { metamaskErrorWrap } from "../../../helpers/metamask-error-wrap";

export function PositionList() {
	const dispatch = useDispatch();
  const { connected, provider } = useWeb3Context();   
  const {perpetualObject} = Trade.useContainer();
  const {slippage} = Trade.useContainer();
  const {marketticker} = AMM.useContainer();
  let tickerSymbol = marketticker;
  const outputSymbol = marketticker.substring(0, marketticker.length - 3);

	const isOpenedPositionCalculated = useSelector<IReduxState, boolean>((state) => state.positions.isOpenedPositionCalculated);

	const isLoss = useSelector<IReduxState, boolean>(state => {
		return state.positions.isLoss;
	});

	const posSize = useSelector<IReduxState, string>(state => {
		return state.positions.posSize;
	});


	const side = useSelector<IReduxState, PositionSide>(state => {
		return state.positions.side;
	});

	const pendingValue = useSelector<IReduxState, string>(state => {
		return state.positions.pendingValue;
	});

	const isProfit = useSelector<IReduxState, boolean>(state => {
		return state.positions.isProfit;
	});

	const pnlValue = useSelector<IReduxState, string>(state => {
		return state.positions.pnlValue;
	});

	const openPrice = useSelector<IReduxState, string>(state => {
		return state.positions.openPrice;
	});

	const indexPrice = useSelector<IReduxState, string>(state => {
		return state.positions.indexPrice;
	});

	const liquidationPrice = useSelector<IReduxState, string>(state => {
		return state.positions.liquidationPrice;
	});

	const marginRatio = useSelector<IReduxState, string>(state => {
		return state.positions.marginRatio;
	});

	const leverage = useSelector<IReduxState, string>(state => {
		return state.positions.leverage;
	}); 

	const handleClose = async () => {		
		const hasconnected = perpetualObject.hasConnected();  
		if(hasconnected === false) {
			await perpetualObject.init();
			await perpetualObject.connect({ signer: provider.getSigner() });	
		}
		const position = await perpetualObject?.positions?.getTakerPositionByTickerSymbol(tickerSymbol);
		if(position !== undefined) {			
			let closeTx;
			try {
				closeTx = await perpetualObject?.clearingHouse?.closePosition(position!, slippage);
				console.log("Close Transaction", closeTx);
				await closeTx!.transaction.wait();
        dispatch(success({ text: messages.tx_successfully_send }));
        await sleep(0.5);
        dispatch(info({ text: messages.your_position_close_soon }));
			} catch (err: any) {
				metamaskErrorWrap(err, dispatch);
			} finally {
				dispatch(close());				
			}			
		} 
	};

	return (
		<Box>
			{isOpenedPositionCalculated && 
				<Box sx={{maxWidth: '360px'}}>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Box sx={{display: 'flex', justifyContent: 'flex-start', alignItems: 'center'}}>              
							<Typography sx={{fontSize: '16px', mr: 1, color: 'white'}}>{posSize}</Typography>
							<Typography sx={{fontSize: '12px', color: '#a3a8b7'}}>{outputSymbol}</Typography>
						</Box>
						<Typography sx={{fontSize: '14px', color: side==='LONG'? 'rgba(94, 217, 151, 1)': 'rgba(234, 98, 98, 1)'}}>{side}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Pending Funding Payment</Typography>
						<Typography sx={{fontSize: '14px', color: isLoss===true? 'rgba(234, 98, 98, 1)': 'rgba(94, 217, 151, 1)'}}>{pendingValue}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Profit / Loss</Typography>
						<Typography sx={{fontSize: '14px', color: isProfit===true? 'rgba(94, 217, 151, 1)': 'rgba(234, 98, 98, 1)'}}>{pnlValue}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Avg. Open Price</Typography>
						<Typography sx={{fontSize: '14px', color: 'white'}}>{openPrice}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Index Price</Typography>
						<Typography sx={{fontSize: '14px', color: 'white'}}>{indexPrice}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Liquidation Price</Typography>
						<Typography sx={{fontSize: '14px', color: 'white'}}>{liquidationPrice}</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Margin Ratio</Typography>
						<Typography sx={{fontSize: '14px', color: 'white'}}>{marginRatio}%</Typography>
					</Box>
					<Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
						<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Leverage</Typography>
						<Typography sx={{fontSize: '14px', color: 'white'}}>{leverage}x</Typography>
					</Box>
					<Box sx={{display: 'flex'}}>
						<Button variant="contained" 
							disabled={connected === false}
							sx={{paddingY: '8px', mt: 1, px: 4,
								backgroundColor: '#185993', 
								backgroundImage: 'linear-gradient(to right,#185993 0,#236dae 51%,#185993 100%)',
								backgroundSize: '200%',
								boxShadow: '5px 5px 10px 0 rgba(24,89,147,0.25)',
								color: '#a3a8b7',
								'&: hover': {backgroundColor: '#2376c0', color: 'white', backgroundPosition: 'right center'}								
							}}
							onClick={handleClose}			
						>
							Close
						</Button>
					</Box>
				</Box>
			}
		</Box>
	);
}