import React from "react";
import ReactDOM from "react-dom";
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import store from "./store/store";
import { Provider } from "react-redux";
import { Web3ContextProvider } from "./hooks/web3Context";
import { SnackbarProvider } from "notistack";
import SnackMessage from "./components/Messages/snackbar";
import {Trade} from './container/trade';
import { AMM } from './container/amm';

const Providers = ((...providers: any[]) => ({ children }: { children: React.ReactNode }) => {
  return providers.reduceRight((providers, provider) => {
      const Provider = provider.component || provider
      const props = provider.props || {}
      return <Provider {...props}>{providers}</Provider>
  }, children)
})(  
  Trade.Provider,  
  AMM.Provider
)

ReactDOM.render(
  <React.StrictMode>  
    <SnackbarProvider
      maxSnack={4}
      anchorOrigin={{
          vertical: "bottom",
          horizontal: "right",
      }}
      content={(key: string | number, message: string) => <SnackMessage id={key} message={JSON.parse(message)} />}
      autoHideDuration={10000}
    >
      <Provider store={store}>
        <Web3ContextProvider>
          <Providers>
          <App />      
          </Providers>
        </Web3ContextProvider>
      </Provider>
    </SnackbarProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
