import { createSlice,createAsyncThunk, createSelector } from "@reduxjs/toolkit";
import { JsonRpcProvider, StaticJsonRpcProvider } from "@ethersproject/providers";
import { Networks, DEFAULD_NETWORK } from "../../constants/blockchain";
import { setAll } from "../../helpers/set-all";
import { PerpetualProtocol, PositionSide, PositionDraft } from "@perp/sdk-curie";
import Big from "big.js";
import { RootState } from "../store";

interface ICalcPositionDetails { 
	connected: Boolean;   
  networkID: Networks;  
	provider: StaticJsonRpcProvider | JsonRpcProvider;
	perpetualObject: PerpetualProtocol;
	side: PositionSide;
	slippage: Big;
	tickerSymbol: string;
	amountInput: Big;	
}

export const calcPositionDetails = createAsyncThunk("position/calcPositionDetails", 
	async({connected, networkID, provider, perpetualObject, side, slippage, tickerSymbol, amountInput}: ICalcPositionDetails) => {	
		const isAmountInputBase = false;	
		let buyingPowerBound: Big;
							
		if(amountInput === null) {
			return new Promise<any>(resevle => {
				resevle({
					oppositeAmount: "",					
					newPositionDraft: undefined,
					entryPrice: "",
					tradingFee: "",
					priceImpact: "",
					buyingPower: "",					
				});
			});
		}

		if(amountInput.eq(0)) {
			return new Promise<any>(resevle => {
				resevle({
					oppositeAmount: "0",					
					newPositionDraft: undefined,
					entryPrice: "",
					tradingFee: "",
					priceImpact: "",
					buyingPower: "",					
				});
			});
		}	
		
		await perpetualObject.init();		

		const newPositionDraft = perpetualObject.clearingHouse?.createPositionDraft({
			tickerSymbol,
			side,
			amountInput,
			isAmountInputBase
		});	
		console.log("NewPosition::", newPositionDraft);	
		
		const entryPriceBound = await newPositionDraft!.getEntryPrice();
		const tradingFeeBound = await newPositionDraft!.getTransactionFee();
		const priceImpactBound = await newPositionDraft!.getPriceImpact();		
		const oppositeAmountBound = await newPositionDraft!.getOppositeAmountBound(slippage);					

		if( connected === true && networkID === DEFAULD_NETWORK ) {
			await perpetualObject.connect({ signer: provider.getSigner() });
			buyingPowerBound = await newPositionDraft!.getBuyingPower();	
			return {
				oppositeAmount: oppositeAmountBound!.toFixed(7),		
				newPositionDraft,
				entryPrice: entryPriceBound!.toFixed(2),
				tradingFee: tradingFeeBound!.toFixed(8),
				priceImpact: priceImpactBound!.toFixed(5),
				buyingPower: buyingPowerBound!.toFixed(4),
			};
		}			
		
		return {
			oppositeAmount: oppositeAmountBound!.toFixed(7),		
			newPositionDraft,
			entryPrice: entryPriceBound!.toFixed(2),
			tradingFee: tradingFeeBound!.toFixed(8),
			priceImpact: priceImpactBound!.toFixed(5),
			buyingPower: '',
		};
});

interface IOpenedPositionDetails {   
	address: string; 
  networkID: Networks;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
	perpetualObject: PerpetualProtocol;	
	tickerSymbol: string;	
}

export const openedPositionDetails = createAsyncThunk("position/openedPositionDetails", 
	async({address, networkID, provider, perpetualObject, tickerSymbol}: IOpenedPositionDetails, { dispatch }) => {
		if( networkID === DEFAULD_NETWORK ) {        
			const hasconnected = perpetualObject.hasConnected(); 			
			if(hasconnected === false) {
				await perpetualObject.init();
				await perpetualObject.connect({ signer: provider.getSigner() });
			}		
			const position = await perpetualObject?.positions?.getTakerPositionByTickerSymbol(tickerSymbol);  
			if(position !== undefined) {
				dispatch(open());          
			} else {
				dispatch(close());				
				return new Promise<any>(resevle => {
					resevle({
						isLoss: false,					
						posSize: '',
						side: PositionSide.LONG,
						pendingValue: '',
						isProfit: false,
						pnlValue: '',
						openPrice: '',
						indexPrice: '',
						liquidationPrice: '',
						marginRatio: '',
						leverage: ''										
					});
				});				
			}		
			
			const _posSize = position!.sizeAbs.toFixed(8);			
			const _side = position!.side;		
			// const pendingValue_ = await perpetualObject.contractReader.getPendingFundingPayments(Marketmap, address); 	 
			// console.log("Pending value", pendingValue_);
			const _pendingValue = await perpetualObject!.positions!.getTotalPendingFundingPayments(); 
			let _isLoss;      
			if(_pendingValue![tickerSymbol].s !== -1) {
				_isLoss = false;
			} else {
				_isLoss = true;         
			}	

			const pnlValueCalc = await perpetualObject!.positions!.getTakerPositionByTickerSymbol(tickerSymbol);
			const _pnlValue = await pnlValueCalc!.getUnrealizedPnl();
			let _isProfit;  
			if(_pnlValue.s !== -1) {
				_isProfit = true;
			} else {
				_isProfit = false;         
			}  
			const _openPrice = position!.entryPrice.toFixed(2);
			const _indexPrice = await perpetualObject.contractReader.getIndexPrice(position!.market.baseAddress);         
			const _liquidationPrice = position!.liquidationPrice; 			
			
			const _marginRatio = await perpetualObject?.positions?.getAccountMarginRatio();
			const _leverage = await perpetualObject?.positions?.getAccountLeverage();

			return {
				isLoss: _isLoss,
				posSize: _posSize,
				side: _side,
				pendingValue: _pendingValue![tickerSymbol].toFixed(4),
				isProfit: _isProfit,
				pnlValue: _pnlValue.toFixed(4),
				openPrice: _openPrice,
				indexPrice: _indexPrice.toFixed(2),
				liquidationPrice: _liquidationPrice!.toFixed(1),
				marginRatio: (Number(_marginRatio!.toFixed(4))*100).toFixed(1),
				leverage: _leverage!.toFixed(2)
			}
		}
});	

interface ICalcMarketDetails { 
	perpetualObject: PerpetualProtocol;	
	tickerSymbol: string;
}

export const calcMarketDetails = createAsyncThunk("market/calcMarketDetails", 
	async({ perpetualObject, tickerSymbol }: ICalcMarketDetails) => {
		await perpetualObject.init();
		const Marketmap = perpetualObject.markets.marketMap;	
		const _marketIndexPrice = await perpetualObject.contractReader.getIndexPrice(Marketmap[tickerSymbol].baseAddress);
		console.log("Update index price", _marketIndexPrice.toFixed(2));
		return {
			marketIndexPrice: _marketIndexPrice.toFixed(2)
		}
});

const initialState = {
	isPositionOpened: false,	
	isCalculated: false,
	isOpenedPositionCalculated: false,
	isMarketInfoCalculated: false,
	isMarketInfoInitialized: false,
}

export interface IPositionSlice {
	isPositionOpened: boolean;	
	isCalculated: boolean;
	isOpenedPositionCalculated: boolean;
	isMarketInfoCalculated: boolean;
	isMarketInfoInitialized: boolean;
	oppositeAmount: string,	
	newPositionDraft: PositionDraft<string> | undefined,
	entryPrice: string,
	tradingFee: string,
	priceImpact: string,
	buyingPower: string,
	isLoss: boolean,
	posSize: string,
	side: PositionSide,
	pendingValue: string,
	isProfit: boolean,
	pnlValue: string,
	openPrice: string,
	indexPrice: string,
	liquidationPrice: string,
	marginRatio: string,
	leverage: string,
	marketIndexPrice: string,
}

const positionsSlice = createSlice({
	name: "positions",
	initialState,
	reducers: {
		open(state)	{
			state.isPositionOpened = true;
			state.isCalculated = false;
		},		
		close(state) {
			state.isPositionOpened = false;
			state.isOpenedPositionCalculated = false;
		}		
	},
	extraReducers: builder => {
		builder
			.addCase(calcPositionDetails.pending, state => {
				state.isCalculated = false;
			})
			.addCase(calcPositionDetails.fulfilled, (state, action) => {
				setAll(state, action.payload);
				state.isCalculated = true;
			})
			.addCase(calcPositionDetails.rejected, (state, { error }) => {
				state.isCalculated = false;
				console.log(error);
			})
			.addCase(openedPositionDetails.pending, state => {			
				state.isOpenedPositionCalculated = false;	
			})
			.addCase(openedPositionDetails.fulfilled, (state, action) => {
				setAll(state, action.payload);				
				state.isOpenedPositionCalculated = true;				
			})
			.addCase(openedPositionDetails.rejected, (state, { error }) => {				
				state.isOpenedPositionCalculated = false;
				console.log(error);
			})
			.addCase(calcMarketDetails.pending, state => {			
				state.isMarketInfoCalculated = false;
			})
			.addCase(calcMarketDetails.fulfilled, (state, action) => {
				setAll(state, action.payload);
				state.isMarketInfoCalculated = true;	
				state.isMarketInfoInitialized = true;
			})
			.addCase(calcMarketDetails.rejected, (state, { error }) => {
				state.isMarketInfoCalculated = false;	
				console.log(error);
			})
	},    
});

const baseInfo = (state: RootState) => state.positions;
export const { open, close } = positionsSlice.actions;

export default positionsSlice.reducer;

export const getAppState = createSelector(baseInfo, positions => positions);